<template>
  <div class="video-wrapper" :style="{width: `${width}`}">
    <div class="ratio ratio-16x9 cover" v-if="cover" :class="{'d-none': hideCover}" :style="{ backgroundImage: `url(${cover})` }">
      <button type="button" class="btn-play" @click="play">
        <img src="~@/assets/images/video_play.svg"/>
      </button>
    </div>

    <div class="ratio ratio-16x9 video" v-if="src">
      <LazyYoutube :src="src" ref="lazyVideo" :showTitle="false" />
    </div>
    <div class="ratio ratio-16x9 border-1" v-if="border=='1'" :style="{width: `${width}`}"></div>
  </div>
</template>

<script>
import { LazyYoutube } from "vue-lazytube";

export default {
  name: 'VideoWidget',
    components: {
        LazyYoutube,
    },
  data () {
    return {
      hideCover: false
    }
  },
  props: {
    src: String,
    cover: String,
    border: String,
    width: {
      type: String,
      default: '100%'
    },
  },
  methods: {
    play: function() {
      this.hideCover = true;
      this.$refs["lazyVideo"]['playVideo']()
    }
  }
}
</script>

