  @import '~bootstrap/scss/bootstrap';

  .video-wrapper {
    position: relative;
    @include media-breakpoint-down(xl) {
      width: 100% !important;
      margin: 2em auto;
    }

    .border-1 {
      border: 0.8px solid white;
      border-radius: 7%/13%;
      position: absolute;
      z-index: 0;
      top: -1em;
      right: -1em;
      @include media-breakpoint-down(xl) {
        width: 100% !important;
        top: -10px;
        right: -10px;
      }
    }

    .video {
      border-radius: 7%/13%;
      overflow: hidden;
      z-index: 1;

      & > div {
        max-width: 100%;
      }
    }

    .cover {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 7%/13%;
      overflow: hidden;

      .btn-play {
        background: none;
        border: none;
        opacity: 0.9;
        z-index: 1;
        &:hover {
          opacity: 1;
        }

        img {
          width: 20%;
        }
      }
    }
  }

</style>
