<template>
    <div id="menu-hamburger" @click="toggleOpen" :class="{'open': is_expanded}">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
</template>

<script>
export default {
  name: 'MenuHamburger',
  props: {
    'expanded': {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      is_expanded: this.$props.expanded
    }
  },
  watch: {
    expanded: function(val) {
      this.is_expanded = val;
    }
  },
  methods: {
    toggleOpen: function () {
      this.is_expanded = !this.is_expanded;
      this.$emit('click', this.is_expanded);
    }
  }
}
</script>

<style lang="scss" scoped>
  #menu-hamburger {
    width: 30px;
    height: 24px;
    position: relative;
    cursor: pointer;
    span {
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      background: white;
      border-radius: 9px;
      opacity: 1;
      left: 0;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: .15s ease-in-out;
      -moz-transition: .15s ease-in-out;
      -o-transition: .15s ease-in-out;
      transition: .15s ease-in-out;
      &:nth-child(1) {
        top: 0px;
      }
      &:nth-child(2), &:nth-child(3) {
        top: 10px;
      }
      &:nth-child(4) {
        top: 20px;
      }
    }
    &.open span {
      &:nth-child(1) {
        top: 10px;
        width: 0%;
        left: 50%;
      }

      &:nth-child(2) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
      }

      &:nth-child(3) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }

      &:nth-child(4) {
        top: 10px;
        width: 0%;
        left: 50%;
      }
    }
  }
</style>